<template>
  <el-card class="host">
    <div class="hostInfo">
      <div>
        <div>{{ host.hypervisor_hostname }}</div>
        <div>IP:{{ host.host_ip }}</div>
        <div>:{{ host.running_vms }}</div>
      </div>
      <div id="infoEcharts"></div>
    </div>
  </el-card>
</template>

<script>
  export default {
    name: 'hostInfo',
    mounted: function () {
      console.log(this.host)
      this.$nextTick(() => {
        let memory_used = Math.round(this.host.memory_mb_used/this.host.memory_size*10000)/100
        let local_gb_used = Math.round(this.host.local_gb_used/this.host.local_gb*10000)/100
        // 
        this.$echarts.init(document.getElementById('infoEcharts')).setOption({
          legend: {
            data: ['host']
          },
          series: [

            {
              name: '',
              type: 'gauge',
              center: ['150', '50%'], // 
              radius: '100%',
              min: 0,
              max: 100,
              endAngle: 45,
              splitNumber: 10,
              axisLine: { // 
                lineStyle: { // lineStyle
                  width: 8
                }
              },
              axisTick: { // 
                length: 12, // length
                lineStyle: { // lineStyle
                  color: 'auto'
                }
              },
              splitLine: { // 
                length: 20, // length
                lineStyle: { // lineStylelineStyle
                  color: 'auto'
                }
              },
              pointer: {
                width: 5
              },
              title: {
                offsetCenter: [0, '-30%'], // x, ypx
              },
              detail: {
                // TEXTSTYLE
                fontWeight: 'bolder'
              },
              data: [{
                value: memory_used,
                name: ' %'
              }, ]
            },

            {
              name: '',
              type: 'gauge',
              center: ['300', '50%'], // 
              radius: '100%',
              min: 0,
              max: 100,
              splitNumber: 10,
              axisLine: { // 
                lineStyle: { // lineStyle
                  width: 8
                }
              },
              axisTick: { // 
                length: 12, // length
                lineStyle: { // lineStyle
                  color: 'auto'
                }
              },
              splitLine: { // 
                length: 20, // length
                lineStyle: { // lineStylelineStyle
                  color: 'auto'
                }
              },
              pointer: {
                width: 5
              },
              title: {
                offsetCenter: [0, '-30%'], // x, ypx
              },
              detail: {
                // TEXTSTYLE
                fontWeight: 'bolder'
              },
              data: [{
                value: local_gb_used,
                name: ' %'
              }, ]
            },

            {
              name: 'VCPU',
              type: 'gauge',
              center: ['420', '50%'], // 
              radius: '60%',
              min: 0,
              max: this.host.vcpus,
              startAngle: 75,
              endAngle: -75,
              splitNumber: 1,
              axisLine: { // 
                lineStyle: { // lineStyle
                  width: 4
                }
              },
              axisTick: { // 
                length: 8, // length
                lineStyle: { // lineStyle
                  color: 'auto'
                }
              },
              splitLine: { // 
                length: 15, // length
                lineStyle: { // lineStylelineStyle
                  color: 'auto'
                }
              },
              pointer: {
                width: 2
              },
              title: {
                offsetCenter: [0, '-30%'], // x, ypx
              },
              detail: {
                // TEXTSTYLE
                fontWeight: 'bolder'
              },
              data: [{
                value: this.host.vcpus_used,
                name: 'CPU'
              }, ]
            },
          ]
        })

      })
    },
    props: [
      'host'
    ]
  }
</script>

<style>
  .hostInfo {
    display: flex;
    flex-direction: row;
  }

  .host {
    margin: 10px;
  }

  #infoEcharts {
    width: 70%;
    height: 200px;
  }
</style>


