<template>
  <div class="tableBox">
    <el-row class="right">
      <createNetwork></createNetwork>
      <deleteNetwork :multipleSelection="multipleSelection"></deleteNetwork>
    </el-row>
    <el-table ref="multipleTable" :data="tableData3" tooltip-effect="dark" v-loading="table_loading" style="width: 100%"
      @selection-change="handleSelectionChange">
      <el-table-column type="selection" width="55">
      </el-table-column>
      <el-table-column prop="name" label="">
      </el-table-column>
      <el-table-column label="" width="240">
        <template slot-scope="scope">
          <subNet :subnets="scope.row.subnets_info"></subNet>
        </template>
      </el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">
          {{ scope.row.shared | shared }}
        </template>
      </el-table-column>
      <el-table-column prop="is_router_external" label=""></el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">
          {{ scope.row.status | status }}
        </template>
      </el-table-column>
      <el-table-column prop="admin_state_up" label="">
        <template slot-scope="scope">
          {{ scope.row.admin_state_up | admin_state_up }}
        </template>
      </el-table-column>
      <el-table-column prop="availability_zones" label="">
      </el-table-column>
      <el-table-column fixed="right" label="" width="120">
        <template slot-scope="scope">
          <el-dropdown trigger="click">
            <el-button size="mini">
              <i class="el-icon-arrow-down el-icon--right"></i>
            </el-button>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item @click.native="updNetworkDialog(scope.row)"></el-dropdown-item>
              <el-dropdown-item @click.native="creSubnetDialog(scope.row)"></el-dropdown-item>
              <el-dropdown-item @click.native="delNetworkDialog(scope.row)"></el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </template>
      </el-table-column>
    </el-table>

    <!-- dialog -->
    <el-dialog title="" :visible.sync="updateNetwork.dialog" width="400px">
      <el-form label-width="140px">
        <el-form-item label="">
          <el-input v-model="updateNetwork.network_name" size="mini"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-checkbox v-model="updateNetwork.admin_state_up" size="mini"></el-checkbox>
        </el-form-item>
        <el-form-item label="">
          <el-checkbox v-model="updateNetwork.is_share"></el-checkbox>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="updateNetwork.dialog = false"> </el-button>
        <el-button type="primary" @click="updNetwork()"> </el-button>
      </span>
    </el-dialog>

    <!-- dialog -->
    <el-dialog title="" :visible.sync="createSubnet.dialog" width="400px">
      <el-tabs v-model="activeName">
        <el-tab-pane label="" name="subnet">
          <el-form label-width="100px">
            <el-form-item label="">
              <el-input v-model="createSubnet.name" size="mini"></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-input v-model="createSubnet.cidr" size="mini"></el-input>
            </el-form-item>
            <el-form-item label="IP">
              <el-select v-model="createSubnet.ip_version" placeholder="" size="mini">
                <el-option v-for="v in createSubnet.IPs" :key="v.value" :value="v.value" :label="v.name"></el-option>
              </el-select>
            </el-form-item>
            <!-- <el-form-item label="IP">
          <el-input v-model="createSubnet"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-checkbox v-model="createSubnet"></el-checkbox>
        </el-form-item> -->
          </el-form>
        </el-tab-pane>
        <el-tab-pane label="" name="subnetInfo">
          <el-form label-width="100px">
            <el-form-item label="">
              <el-input type="textarea" :autosize="{ minRows: 2, maxRows: 4}" placeholder="" v-model="createSubnet.allocation_pools">
              </el-input>
            </el-form-item>
          </el-form>
        </el-tab-pane>
      </el-tabs>

      <span slot="footer" class="dialog-footer">
        <el-button @click="createSubnet.dialog = false"> </el-button>
        <el-button type="primary" @click="creSubnet()"> </el-button>
      </span>
    </el-dialog>

    <!-- dialog -->
    <el-dialog title="" :visible.sync="deleteNetwork.dialog" width="500px">
      <el-form label-width="100px">
        
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="deleteNetwork.dialog = false"> </el-button>
        <el-button type="primary" @click="delNetwork()"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
  import axios from 'axios'
  import subNet from './table_column/subnet'
  import createNetwork from './menu/network/createNetwork'
  import deleteNetwork from './menu/network/deleteNetwork'

  export default {
    data() {
      return {
        activeName: 'subnet',
        tableData3: [],
        multipleSelection: [],
        updateNetwork: {
          dialog: false,
          network_name: '',
          admin_state_up: '',
          is_share: false
        },
        createSubnet: {
          dialog: false,
          name: '',
          IPs: [{
              name: 'IPv4',
              value: 4
            },
            {
              name: 'IPv6',
              value: 6
            }
          ],
          ip_version: 4
        },
        deleteNetwork: {
          dialog: false
        },
        table_loading: true,
      }
    },
    components: {
      subNet,
      createNetwork,
      deleteNetwork
    },
    beforeCreate: function () {
      getNetworkList(this)
    },
    filters: {
      shared: function (value) {
        return value ? '' : ''
      },
      status: function (value) {
        return value == 'ACTIVE' ? '' : ''
      },
      admin_state_up: function (value) {
        return value ? 'UP' : 'Down'
      }
    },
    methods: {
      toggleSelection(rows) {
        if (rows) {
          rows.forEach(row => {
            this.$refs.multipleTable.toggleRowSelection(row);
          });
        } else {
          this.$refs.multipleTable.clearSelection();
        }
      },
      handleSelectionChange(val) {
        this.multipleSelection = val;
      },
      updNetworkDialog(network) {
        this.updateNetwork.network_name = network.name
        this.updateNetwork.admin_state_up = network.admin_state_up
        this.updateNetwork.is_share = network.shared
        this.updateNetwork.dialog = true
        this.updateNetwork.id = network.id
      },
      creSubnetDialog(network) {
        this.createSubnet.dialog = true
        this.createSubnet.id = network.id
      },
      delNetworkDialog(network) {
        this.deleteNetwork.dialog = true
        this.deleteNetwork.id = network.id
      },
      updNetwork() {
        this.updateNetwork.dialog = false
        axios.post('/network/updateNetwork', {
          network_id: this.updateNetwork.id,
          network_name: this.updateNetwork.network_name,
          admin_state_up: this.updateNetwork.admin_state_up,
          is_share: this.updateNetwork.is_share
        })
      },
      creSubnet() {
        this.createSubnet.dialog = false
        axios.post('/network/createSubnet', {
          network_id: this.createSubnet.id,
          name: this.createSubnet.name,
          cidr: this.createSubnet.cidr,
          ip_version: this.createSubnet.ip_version,
          allocation_pools: this.createSubnet.allocation_pools
        })
      },
      delNetwork() {
        this.deleteNetwork.dialog = false
        axios.post('/network/deleteNetwork', {
            network_id: this.deleteNetwork.id
          })
          .then(() => {

          })
      }
    }
  }

  function getNetworkList(vue) {
    axios.get('/network/listnetworks')
      .then((res) => {
        vue.tableData3 = res.data['networkInfo']
        vue.table_loading = false
      })
  }
</script>

<style scoped>

</style>


