function instance_states(value){
  switch (value) {
    case 'active':
      return ''
      break;
    case 'building':
      return ''
      break;
    case 'deleted':
      return ''
      break;
    case 'error':
      return ''
      break;
    case 'hard_reboot':
      return ''
      break;
    case 'password':
      return ''
      break;
    case 'paused':
      return ''
      break;
    case 'reboot':
      return ''
      break;
    case 'rebuild':
      return ''
      break;
    case 'rescued':
      return ''
      break;
    case 'resized':
      return ''
      break;
    case 'revert_resize':
      return ''
      break;
    case 'shutoff':
      return ''
      break;
    case 'soft_deleted':
      return ''
      break;
    case 'stopped':
      return ''
      break;
    case 'suspended':
      return ''
      break;
    case 'verify_resize':
      return ''
      break;
    case 'shelved_offloaded':
      return ''
      break;

    default:
      return ''
      break;
  }
}

function power_states(value){
  switch (value) {
    case 0:
      return ''
      break;
    case 1:
      return ''
      break;
    case 3:
      return ''
      break;
    case 4:
      return ''
      break;
    case 6:
      return ''
      break;
    case 7:
      return ''
      break;
  }
}

function instance_address(values){
  let addrs = []
  for(let key in values){
    let value = values[key]
    for(let k in value){
      let ip = value[k]
      addrs.push(ip.addr)
    }
  }
  return addrs
}

function task_states(values) { 
  return values ? values : ''
}

export default {
  instance_states,
  instance_address,
  power_states,
  task_states
}


